.cellanno-container {
  display: flex;
  padding: 5px;
  flex-direction: column;
  font-size: x-small;
}

.cellanno-container-header {
  display: flex;
  justify-content: space-between;
}

.cellanno-cluster-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 22px;
}

.cellanno-cluster-selection {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
}

.cellanno-cluster-selection-width {
  width: 100%;
  margin: 0 2px;
}

.cellanno-table {
  display: grid;
  /* grid-template-rows: 35px calc(100vh - 290px); */
  grid-template-columns: auto;
  padding: 5px 0 0 0;
  /* gap: 5px; */
}

/* .cellanno-header {
  grid-row: 1;
  grid-column: 1/ -1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
} */

.cellanno-list {
  grid-row: 1 / -1;
  grid-column: 1 / -1;
  text-align: start;
  overflow-x: hidden;
  height: 100% !important;
  font-size: small;
}

.cellann-row-container {
  display: grid;
  /* grid-template-columns: 25% 14% 17% 25% 17%; */
  gap: 2px;
  align-items: center;
}

.cellann-row-header {
  /* font-size: 12px; */
  text-align: center;
  align-items: center;
}

.cellann-row-action {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 3px;
}
